import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Button.css';


export class Button extends Component {

  render() {
    const { type } = this.props;
    switch (type) {
      case 'link':
        return this.renderLink();
      case 'file':
        return this.renderFileInput();
      default:
        return this.renderButton();
    }
  }


  renderFileInput() {
   const { text } = this.props;

   return <label className="Button">
    { text }
    <input type="file"
      className="Button__input_file"
      onChange={this.onFileInputChanged.bind(this)}
    />
   </label>;
  }


  renderLink() {
    const { href, downloadFileName, text } = this.props;
    const linkProps = {
      download: downloadFileName || null
    };

    return <a href={ href }
      { ...linkProps}
      className="Button"
    >
      { text }
    </a>;
  }


  renderButton() {
    const { type, text, isDisabled } = this.props;
    const className = `Button ${isDisabled ? 'Button_disabled' : ''}`;
    const btnProps = {
      type: type || 'button',
      disabled: isDisabled ? 'disabled' : null
    };

    return <button {...btnProps}
      onClick={this.onBtnPress.bind(this)}
      className={className}
    >
      { text }
    </button>;
  }


  onFileInputChanged(event) {
    const files = event.target.files;
    const file = files && files[0];
    this.props.onFileSelected(file);
  }


  onBtnPress() {
    const { onPress } = this.props;
    if (onPress) {
      onPress();
    }
  }

}


Button.propTypes = {
  //   href,          ,
  //  href    href .
  //  isDisabled, type, onPress  .
  href: PropTypes.any,
  //   ,      
  //   download  ,     .
  downloadFileName: PropTypes.string,
  isDisabled: PropTypes.bool,
  //    button ( )  submit  :
  // file -   input   ,
  // link -      .
  type: PropTypes.string,
  onPress: PropTypes.func,
  //    type=file     ,    .
  //       .
  onFileSelected: PropTypes.func,
  text: PropTypes.string
};



